@import url(https://deploy-preview-13--agri-component.netlify.app/css/components.css);
@import url(https://deploy-preview-13--agri-component.netlify.app/css/components.css);
* {
  scroll-behavior: smooth;
  padding: 0;
  margin: 0;
}
a {
  text-decoration: none;
}

:root {
  --sky-blue-color: rgb(129, 129, 255);
  --white-color: rgb(255, 255, 255);
  --black-color: rgb(0, 0, 0);
}

:root {
  /* Theme */
  --primary-color: #1662f3;
  --light-color: #d0dffc;
  --dark-color: #283b70;
  --regular-font: "Inter", sans-serif;
}

.chips {
  color: black;
  font-family: "Inter", sans-serif;
  font-family: var(--regular-font);
  font-size: 1rem;
  border-radius: 100rem;
  box-shadow: 0 0 0 2px #d0dffc;
  box-shadow: 0 0 0 2px var(--light-color);
  background-color: transparent;
  background-color: #689dff;
  font-weight: bold;
  transition: background-color 250ms ease, box-shadow 250ms ease;
  margin-top: 5rem;
  width: 15%;
  height: 2rem;
  justify-content: center;
  align-items: center;
  display: flex;
  text-align: center;
  cursor: pointer;
}

.chips:hover {
  background-color: blue;
  color: white;
  box-shadow: 0 0 0 calc(2px + 0.25rem) #d0dffc;
  box-shadow: 0 0 0 calc(2px + 0.25rem) var(--light-color);
}

.chips-container {
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  width: 50%;
  margin-left: 12rem;
}

* {
  margin: 0;
  padding: 0;
}

.footer-items {
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-direction: row;
  height: 4rem;
  background-color: rgb(108, 170, 251);
  max-width: 100%;
  padding: 1rem;
  bottom: 0;
  position: fixed;
  width: 100%;
  font-size: 1.2rem;
}

.social-media-links {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: row;
  height: 2rem;
  max-width: 100%;
  font-size: 1.5rem;
}

.fa {
  margin: 0.8rem;
}

.navigation-menu {
  position: fixed;
  width: 100%;
  top: 0;
}

span.material-icons.navigationmi {
  font-size: 1.8rem;
}

.video__cards {
  height: 15rem;
  width: 20rem;
  border: 1px solid rgb(180, 180, 180);
  background-color: white;
  transition: -webkit-transform 100ms ease-in-out;
  transition: transform 100ms ease-in-out;
  transition: transform 100ms ease-in-out, -webkit-transform 100ms ease-in-out;
  border-radius: 0.3rem;
  box-shadow: 1px 1px 1px 3px black;
  margin: 0.5rem 0 2rem 2rem;
}

.video__cards:hover {
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
}
.video__cards-iframe-videos {
  height: 10.5rem;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
}

iframe {
  border: none;
  padding: 0;
  margin: 0;
}
.video__cards-title {
  padding: 0.3rem;
}
.video__cards-details {
  display: flex;
  padding: 0.2rem;
  justify-content: space-between;
  align-items: center;
  background-color: rgb(191, 191, 246);
  border-radius: 0.3rem;
}
button.btn.btn-warning {
  width: 15%;
  padding: 0.2rem;
}

.video__cards-image img {
  height: 3rem;
  width: 3rem;
  border-radius: 1rem;
  border: none;
}

.aside {
  height: 80%;
  width: 6rem;
  background-color: rgb(106, 161, 255);
  text-align: center;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  flex-direction: column;
  font-size: 1.2rem;
  position: fixed;
  top: 5rem;
  left: 1rem;
  border-radius: 0.5rem;
}
.sidebarmi {
  font-size: 1.8rem;
  display: flex;
  justify-content: center;
  align-items: center;
  color: white;
}

/* fetaures catagories  */
.watchlater,
.history,
.likes,
.playlists,
.explore,
.home {
  text-align: center;
  padding: 1.2rem;
  font-size: 1.2rem;
  cursor: pointer;
  transition: width 2s;
}

.explorepage-videos-style {
  display: flex;
  flex-wrap: wrap;
  margin-left: 2rem;
  margin-top: 1rem;
  padding: 1rem;
  justify-content: center;
  align-items: center;
  margin-bottom: 2rem;
}

.hero {
  height: 100vh;
  width: 100%;
  background-color: rgb(0, 0, 0);
  position: fixed;
  top: 4rem;
  left: 8rem;
  border-radius: 0.3rem;
  color: white;
}
span.hero-heading {
  font-family: "Lucida Sans", "Lucida Sans Regular", "Lucida Grande",
    "Lucida Sans Unicode", Geneva, Verdana, sans-serif;
  font-size: 6rem;
  font-weight: 800;
  margin: 3rem;
  display: block;
}
span.hero-para {
  font-family: "Lucida Sans", "Lucida Sans Regular", "Lucida Grande",
    "Lucida Sans Unicode", Geneva, Verdana, sans-serif;
  font-size: 4rem;
  font-weight: bold;
  margin: 3rem;
  color: rgb(122, 199, 0, 6);
}
span.hero-para2 {
  font-family: "Lucida Sans", "Lucida Sans Regular", "Lucida Grande",
    "Lucida Sans Unicode", Geneva, Verdana, sans-serif;
  font-size: 4rem;
  font-weight: bold;
  margin: 3rem;
  color: rgb(122, 199, 0, 6);
  display: flex;
  justify-content: center;
  align-items: center;
}
span.hero-text {
  display: flex;
  justify-content: space-around;
  align-items: flex-start;
  flex-direction: column;
  margin-top: 10rem;
}
button.btn.btn-success {
  height: 3.5rem;
  width: 10rem;
  background-color: rgb(122, 199, 6);
  -webkit-transform: scale(1.2);
          transform: scale(1.2);
  border: none;
  margin-left: 3rem;
}

button.btn.btn-success:hover {
  background-color: rgb(67, 67, 67);
  color: white;
  transition: all 1s;
  border: none;
}


.single-video-card {
  display: flex;
  margin-left: 10rem;
  margin-top: 6rem;

  justify-content: center;
  align-items: center;
  height: 25rem;
  width: 50%;
  background-color: var(--black-color);
}
.single-video-details {
  display: flex;
  margin-left: 10rem;
  justify-content: space-between;
  align-items: center;
  height: 5rem;
  padding: 2rem;
  width: 50%;
  background-color: var(--sky-blue-color);
}
.single-video-page-buttons {
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  width: 38%;
  border: 1px solid var(--white-color);
  height: 2.5rem;
  color: var(--white-color);
  background-color: var(--sky-blue-color);
  transition: -webkit-transform 100ms ease-in-out;
  transition: transform 100ms ease-in-out;
  transition: transform 100ms ease-in-out, -webkit-transform 100ms ease-in-out;
}
.single-video-page-buttons:hover {
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
}
span.singlevideomi {
  font-size: 1.8rem;
  transition: -webkit-transform 100ms ease-in-out;
  transition: transform 100ms ease-in-out;
  transition: transform 100ms ease-in-out, -webkit-transform 100ms ease-in-out;
}
span.singlevideomi:hover {
  -webkit-transform: scale(1.2);
          transform: scale(1.2);
  cursor: pointer;
  color: var(--black-color);
}
#IFRAME_ID {
  height: 100%;
  width: 100%;
}

